@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
:root {
  --text-primary: black;
  --text-secondary: white;
  --bg-primary: #2c3758;
  --bg-secondary: #282828;
  --transition-speed: 500ms;
}

body {
  color: white;
  background-image: radial-gradient(black, #131313);
  margin: 0;
  padding: 0;
  font-family: "Montserrat", sans-serif;
  width: 100%;
}

body::-webkit-scrollbar {
  width: 0.25rem;
}

body::-webkit-scrollbar-track {
  background: black;
}

body::-webkit-scrollbar-thumb {
  background: #282828;
  background: var(--bg-secondary);
}


.nav-bar {
  position: fixed;
  background-color: var(--bg-primary);
  transition: 200ms ease;
  z-index: 1;
}

.nav-bar-link {
  display: flex;
  align-items: center;
  height: 5rem;
  color: var(--text-secondary);
  text-decoration: none;
}

.nav-bar-nav {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100%;
}
.nav-bar-link i {
  width: 2rem;
  min-width: 2rem;
  margin: 0 1.5rem;
}

.nav-logo {
  font-weight: bold;
  text-transform: uppercase;
  margin-bottom: 1rem;
  text-align: center;
  font-size: 1.5rem;
  letter-spacing: 0.3ch;
  color: white;
  background: var(--bg-secondary);
  width: 100%;
}

.nav-bar:hover .logo-icon {
  transform: rotate(-180deg);
  transition: var(--transition-speed);
}

.nav-bar-link:hover {
  background: var(--bg-secondary);
  transition: 100ms ease;
  text-decoration: none;
  color: white;
}
.nav-bar-item {
  width: 100%;
}
.link-text {
  display: none;
  margin-left: 1rem;
}
.logo-text {
  font-weight: bold;
  text-transform: uppercase;
  font-size: 0.75rem;
}
.icon {
  font-size: 2rem;
  text-align: center;
}

/* small and mobile screens */
@media only screen and (max-width: 576px) {
  .nav-bar {
    bottom: 0;
    width: 100vw;
    height: 5rem;
  }

  .nav-logo {
    display: none;
  }

  .nav-bar-nav {
    flex-direction: row;
  }

  .nav-bar-link {
    justify-content: center;
  }

  /* .main {
      margin: 0;
      margin-bottom: 5rem;
    } */
}

/* large screens */
@media only screen and (min-width: 576px) {
  .nav-bar {
    top: 0;
    width: 5rem;
    height: 100vh;
  }

  .nav-bar:hover .link-text {
    display: inline;
  }

  .nav-bar:hover {
    width: 16rem;
  }
}

.profile-photo {
  width: 100%;
}
@media only screen and (max-width: 576px) {
  .profile-photo {
    margin-bottom: 2rem;
  }
}

.bio-text {
  font-size: 1.4rem;
  line-height: 2.5rem;
}

.bio-wrapper {
}

.profile-wrapper {
  padding: 30px 10px 20px 10px;
  background-image: radial-gradient(black, #131313);
  border-bottom: 3px double var(--bg-primary);
  margin-left: 0px;
  margin-right: 0px;
}

@media only screen and (max-width: 576px) {
  .profile-wrapper {
    margin: 0;
  }
}

.project-photo {
  width: 100%;
}

.bold {
  font-weight: bold;
}

.project-title {
  text-decoration: underline;
}

.project-wrapper {
  border-bottom: 3px double var(--bg-primary);
  padding: 20px;
  margin-left: 0px;
  margin-right: 0px;
}

.project-section-title {
  text-transform: uppercase;
  text-decoration: underline;
  font-size: 3rem;
  font-weight: bold;
  text-align: center;
  padding-top: 3.5rem;
  padding-bottom: 3.5rem;
  border-bottom: 3px double var(--bg-primary);
}

#projects {
  border-bottom: 3px solid var(--bg-primary);
  background-image: radial-gradient(black, #131313);
}

.intro {
  height: 50vh;
  background-image: url(/static/media/background.bb0b79bc.jpg);
  margin: 0 auto;
  border-bottom: 3px solid var(--bg-primary);
}

.text-wrapper {
  position: relative;
  text-align: center;
  margin: 0 auto;
}

.jumbo-text::before {
  content: "";
  width: 0%;
  height: 76px;
  background-color: var(--secondary-color);
  position: absolute;
  bottom: -10px;
  -webkit-animation: 1s ease-in-out forwards 2s;
          animation: 1s ease-in-out forwards 2s;
  mix-blend-mode: screen;
}

.jumbo-text {
  font-size: 4rem;
  font-weight: bold;
  transform: translateY(-600px);
  -webkit-animation: 1.5s slideIn ease-in-out forwards 1s;
          animation: 1.5s slideIn ease-in-out forwards 1s;
  z-index: 10;
  opacity: 0;
  position: relative;
}

@-webkit-keyframes slideIn {
  100% {
    transform: translateY(0px);
    opacity: 1;
  }
}

@keyframes slideIn {
  100% {
    transform: translateY(0px);
    opacity: 1;
  }
}

.main {
  margin-left: 5rem;
  animation: fadeIn ease 2s;
  -webkit-animation: fadeIn ease 2s;
  -moz-animation: fadeIn ease 2s;
  -o-animation: fadeIn ease 2s;
  -ms-animation: fadeIn ease 2s;
}
@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@-webkit-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@media only screen and (max-width: 576px) {
  .main {
    margin: 0;
    margin-bottom: 5rem;
    z-index: -1;
  }
}

.submit-btn {
  color: white;
  background: var(--bg-primary);
  margin-top: 1.5rem;
}

.email {
  outline-color: var(--bg-primary);
  width: 100%;
  margin-bottom: 1.5rem;
  border-radius: 0.25rem;
  height: 35px;
}

.message-box {
  outline-color: var(--bg-primary);
  width: 100%;
  border-radius: 0.25rem;
  height: 300px;
}
.center {
  display: flex;
  justify-content: center;
  align-items: center;
}

.contacts-wrapper {
  width: 65%;
  margin: 0 auto;
  padding: 1.5rem;
  border: 3px double var(--bg-primary);
}
.background {
  background-image: radial-gradient(black, #131313);
  height: 100vh;
  margin-left: 5rem;
}
.title {
  font-weight: bold;
  text-decoration: underline;
  text-align: center;
}
@media only screen and (max-width: 576px) {
  .contacts-wrapper {
    width: 80%;
  }
  .background {
    margin-left: 0;
  }
}

